  },
  watch: {
    $route(to) {
      this.goAnchor(to.hash)
    },
  },
  mounted() {
    this.goAnchor(this.$route.hash)
  },
  methods: {
    goAnchor(e) {
      if (e === '#story') {
        this.$store.state.pageTabId = 2
      } else if (e === '#about') {
        this.$store.state.pageTabId = 3
      }
      window.scrollTo({
        top: e && document.querySelector(e).offsetTop - 80 || 0,
        behavior: "smooth"
      })
    }
  }
};
